<template>
    <div class="d-flex flex-column align-items-start">
        <div class="text-uppercase font-weight-bold">统计时间:
            <Space size="large" wrap>
                <DatePicker type="daterange" placement="bottom-end" placeholder="Select date" style="width: 300px" />
            </Space>
        </div>
        <div>
            <avue-crud :data="data" :option="option" @refresh-change="refreshChange" @row-save="rowSave"
                @row-update="rowUpdate" @row-del="rowDel"></avue-crud>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {

            data: [
                {
                    id: 1,
                    name: '',
                    sex: '男'
                }, {
                    id: 2,
                    name: '李四',
                    sex: '女'
                },
            ],
            option: {
                column: [
                    {
                        label: '日期',
                        prop: 'date'
                    }, {
                        label: '账户信息',
                        prop: 'acuntid'
                    }, {
                        label: '所属代理商信息',
                        prop: 'agency'
                    }, {
                        label: '账户总存入（元）',
                        prop: 'acunt'
                    }, {
                        label: '账户总存入非赠款（元）',
                        prop: 'status'
                    }, {
                        label: '账户总存入赠款（元）',
                        prop: 'preliminary'
                    }, {
                        label: '账户总转入（元）',
                        prop: 'consume'
                    }, {
                        label: '账户总转出（元）',
                        prop: 'show'
                    }, {
                        label: '账户总消耗（元）',
                        prop: 'shows'
                    }, {
                        label: '账户非赠款消耗（元）',
                        prop: 'click'
                    }, {
                        label: '账户赠款消耗（元）',
                        prop: 'clickrate'
                    }, {
                        label: '共享赠款消耗（元）',
                        prop: 'ACP'
                    }, {
                        label: '账户总余额（元）',
                        prop: 'percent'
                    }, {
                        label: '账户非赠款余额（元）',
                        prop: 'sac'
                    }, {
                        label: '账户赠款余额（元）',
                        prop: 'percents'
                    }

                ]
            },
        }
    },


    methods: {
        refreshChange() {
            this.$message.success('刷新回调');
        },
        rowSave(form, done, loading) {
            form.id = new Date().getTime();
            this.$message.success('模拟网络请求')
            setTimeout(() => {
                this.$message.success('关闭按钮等待');
                loading();
            }, 1000)
            setTimeout(() => {
                this.$message.success('新增数据' + JSON.stringify(form));
                done(form);
            }, 2000)
        },
        rowDel(form, index, done) {
            this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                done(form)
                this.$message({
                    type: 'success',
                    message: '删除成功!'
                });
            }).catch(() => { });
        },
        rowUpdate(form, index, done, loading) {
            this.$message.success('模拟网络请求')
            setTimeout(() => {
                this.$message.success('关闭按钮等待');
                loading();
            }, 1000)
            setTimeout(() => {
                this.$message.success('编辑数据' + JSON.stringify(form) + '数据序号' + index);
                done(form);
            }, 2000)
        },
    }
}


</script>